<template>
  <button
    class="ucom-button"
    @click="clickHandler"
  >
    {{ btnText }}<slot />
  </button>
</template>

<script>
export default {
  name: 'CustomButton',
  props: {
    url: {
      type: String,
      default: ''
    },
    btnText: {
      type: String,
      default: ''
    }
  },
  methods: {
    clickHandler (event) {
      if (this.url) {
        window.location.href = this.url
        return
      }
      this.$emit('click', event)
    }
  }
}
</script>

<style lang="stylus" scoped>
.ucom-button
  display block
  padding 0 60px
  border none
  border-radius 10px
  color #fff
  font-weight 600
  font-size 1.125rem
  line-height 55px
  background-color $col_primary
  outline none
  box-shadow 2px 3px 10px rgba(0,0,0,0.3)
  cursor pointer
  transition all 150ms

  &:hover
    background-color lighten($col_primary, 5)

  &:active
    box-shadow 0 0 0 rgba(0,0,0,0.3)
</style>
