<template>
  <router-view />
</template>

<script>
export default {
  name: 'App',
  created () {
    this.$store.dispatch('loadSettings')
    this.axios.get('system')
      .then(({ data }) => {
        this.$store.dispatch('setLocale', data.currentCultureCode.substr(0, 2))
        this.$store.dispatch('setCulture', data.currentCultureCode)
        this.$store.dispatch('setUser', data.currentUserFirstName)
        this.$i18n.locale = data.currentCultureCode.substr(0, 2)
      })
  }
}
</script>
