import Vue from 'vue'
import App from './App.vue'
import store from './store'
import router from './router'
import axios from '@/services/http'
import { Format, locales } from '../shared/js/date-formatter'
import Marked from 'marked'
import i18n from '../plugins/vue-i18n'

Vue.config.productionTip = false

Vue.prototype.axios = axios

// Global filters
Vue.filter('format', function (value, format, locale) {
  if (!value) return ''
  value = value.toString()
  return Format(value, format, locales[locale])
})

Vue.filter('markdown', function (value) {
  if (!value) return ''
  value = value.toString()
  return Marked(value, {
    gfm: true,
    breaks: true,
    sanitize: true
  })
})

new Vue({
  store,
  router,
  i18n,
  render: h => h(App)
}).$mount('#app')
