<template>
  <div class="dashboard-container">
    <section class="dashboard">
      <settings-menu @currencyChanged="reloadMetrics" />
      <welcome />
      <metrics-basic />
      <button
        :class="{'expanded': expanded}"
        class="expand-trigger"
        @click="toggleAdvanced"
      >
        {{ triggerText }}
      </button>
      <transition name="fade">
        <metrics-advanced
          v-show="expanded"
          @daysChanged="reloadMetrics"
        />
      </transition>
      <feed />
    </section>
  </div>
</template>

<script>
import Welcome from '../components/welcome-greeting.vue'
import MetricsBasic from '../components/metrics-basic.vue'
import MetricsAdvanced from '../components/metrics-advanced.vue'
import Feed from '../components/feed.vue'
import SettingsMenu from '../components/menu.vue'
export default {
  name: 'Dashboard',
  components: {
    Welcome,
    MetricsBasic,
    MetricsAdvanced,
    Feed,
    SettingsMenu
  },
  data () {
    return {
      metricTimer: null
    }
  },
  computed: {
    expanded () {
      return this.$store.getters.showAdvancedMetrics
    },
    triggerText () {
      return this.expanded ? this.$t('global.collapse') : this.$t('global.expand')
    },
    pollInterval () {
      return this.$store.getters.pollInterval
    },
    daysDisplayed () {
      return this.$store.getters.daysDisplayed
    },
    defaultCurrency () {
      return this.$store.getters.defaultCurrency
    }
  },
  created () {
    this.getCurrencies()
      .then(({ data }) => {
        this.$store.dispatch('setCurrencies', data)
        this.getMetrics()
      })
  },
  methods: {
    toggleAdvanced () {
      this.$store.dispatch('showAdvancedMetrics', !this.expanded)
    },
    reloadMetrics () {
      window.clearTimeout(this.metricTimer)
      this.getMetrics()
    },
    getMetrics () {
      this.axios.get(`metrics/${this.daysDisplayed}/${this.defaultCurrency.value}`)
        .then(({ data }) => {
          this.$store.dispatch('setMetrics', data)
          this.metricTimer = window.setTimeout(() => {
            this.getMetrics()
          }, this.pollInterval)
        })
    },
    getCurrencies () {
      return this.axios.get('currencies')
    }
  }
}
</script>
<!-- eslint-disable-next-line vue-scoped-css/require-scoped -->
<style lang="stylus">
.dashboard-container
  page-container()

.dashboard
  position relative
  width 600px
  margin-top 65px

.expand-trigger
  margin-top 10px
  padding 0
  border none
  color $col_primary
  font-size 0.725rem
  background-color transparent
  outline none
  cursor pointer

  &::after
    margin-left 3px
    font-size 0.5rem
    content ''

  &.expanded::after
    content ''

</style>
