import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '../views/dashboard-main'
import LicenseError from '../views/license-error'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/no-connection',
      name: 'not-connected',
      component: LicenseError,
      props: { errorType: 'noConnection' }
    },
    {
      path: '/not-valid',
      name: 'invalid',
      component: LicenseError,
      props: { errorType: 'invalid' }
    },
    {
      path: '*',
      component: Dashboard
    }
  ]
})

export default router
