import Vue from 'vue'
import Vuex from 'vuex'
import actions from './actions'
import mutations from './mutations'
import getters from './getters'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    locale: 'en',
    culture: '',
    metrics: {},
    user: '',
    pollInterval: 30000,
    showAdvancedMetrics: false,
    currencyMenu: [],
    showMenu: false,
    daysDisplayed: 1,
    feed: [],
    feedTypes: {
      newCustomer: {
        type: 'newCustomer',
        mutable: true,
        muted: false
      },
      returningCustomer: {
        type: 'returningCustomer',
        mutable: true,
        muted: false
      },
      orderPlaced: {
        type: 'orderPlaced',
        mutable: true,
        muted: false
      },
      campaignTriggered: {
        type: 'campaignTriggered',
        mutable: true,
        muted: false
      },
      customerIsCheckingOut: {
        type: 'customerIsCheckingOut',
        mutable: true,
        muted: false
      },
      licenseNetworkUnreachable: {
        type: 'licenseNetworkUnreachable'
      },
      licenseNetworkReachable: {
        type: 'LicenseNetworkReachable'
      }
    },
    licenseError: {}
  },
  getters,
  mutations,
  actions,
  strict: process.env.NODE_ENV !== 'production'
})
