<template>
  <section class="feed">
    <h2 class="feed__heading">
      {{ $t('feed.yourFeed') }}
    </h2>
    <div
      v-if="emptyFeed"
      class="feed__no-content"
    >
      <p>
        {{ $t('feed.noItems') }}
      </p>
      <div class="feed__no-content-icon">
        <spider-icon />
      </div>
    </div>
    <div
      v-if="priorityFeed.length > 0"
      class="feed__priority-list"
    >
      <transition-group name="slide-list">
        <notification
          v-for="item in priorityFeed"
          :key="item.id"
          :notification="item"
        />
      </transition-group>
    </div>
    <transition-group name="slide-list">
      <notification
        v-for="item in feed"
        :key="item.id"
        :notification="item"
      />
    </transition-group>
  </section>
</template>

<script>
import Notification from './feed-item'
import SpiderIcon from '../../shared/icons/legacy/spider.vue'
import { mapGetters } from 'vuex'
export default {
  name: 'Feed',
  components: {
    Notification,
    SpiderIcon
  },
  computed: {
    emptyFeed () {
      return (this.feed.length + this.priorityFeed.length) === 0
    },
    ...mapGetters(['feed', 'priorityFeed', 'pollInterval'])
  },
  created () {
    this.getFeed()
  },
  methods: {
    getFeed () {
      this.axios.get('feed')
        .then(({ data }) => {
          this.$store.dispatch('setFeed', data)
          window.setTimeout(() => {
            this.getFeed()
          }, this.pollInterval)
        })
        .catch(() => {
          window.setTimeout(() => {
            this.getFeed()
          }, this.pollInterval)
        })
    }
  }
}
</script>

<style lang="stylus" scoped>
.feed
  position relative
  margin-top 50px

.feed__heading
  color $col_dark
  font-weight 600
  font-size 1.125rem

.feed__no-content
  display flex
  flex-direction column
  align-items center
  justify-content center
  margin-top 30px
  padding 15px 0 35px
  border solid 1px $col_light_border
  color $col_medium_gray

.feed__no-content-icon
  box(45px)

.feed__priority-list
  margin-bottom 20px

</style>
