<template>
  <div class="license-container">
    <div class="license-page">
      <div class="license__icon">
        <component :is="iconType" />
      </div>
      <h1 class="license__heading">
        {{ heading }}
      </h1>
      <!-- eslint-disable vue/no-v-html -->
      <section
        class="license__text"
        v-html="$options.filters.markdown(bodyText)"
      />
      <div class="license__cta">
        <btn @click="buttonClick">
          {{ buttonText }}
        </btn>
      </div>
      <h3 class="license__error-details-header">
        {{ $t('license.errorDetails') }}
      </h3>
      <section class="license__error-details">
        {{ errorText }}
      </section>
    </div>
  </div>
</template>

<script>
import ExclamationIcon from '../../shared/icons/legacy/exclamation.vue'
import NoConnectIcon from '../../shared/icons/legacy/route-close.vue'
import Btn from '../components/btn.vue'
export default {
  name: 'LicenseError',
  components: {
    ExclamationIcon, // eslint-disable-line vue/no-unused-components
    NoConnectIcon, // eslint-disable-line vue/no-unused-components
    Btn
  },
  props: {
    errorType: {
      type: String,
      default: ''
    }
  },
  computed: {
    invalid () {
      return this.errorType === 'invalid'
    },
    iconType () {
      return this.invalid ? 'ExclamationIcon' : 'NoConnectIcon'
    },
    heading () {
      const translateKey = this.invalid ? 'license.invalidHeading' : 'license.noConnectionHeading'
      return this.$t(translateKey)
    },
    bodyText () {
      const translateKey = this.invalid ? 'license.invalidBody' : 'license.noConnectionBody'
      return this.$t(translateKey)
    },
    buttonText () {
      const translateKey = this.invalid ? 'license.checkLicense' : 'license.checkConnection'
      return this.$t(translateKey)
    },
    errorText () {
      return this.$store.getters.licenseError
    }
  },
  methods: {
    buttonClick () {
      this.axios.get('licensing/licensevalidity')
        .then(({ data }) => {
          // Do something meaningful here - reload if succesful..
        })
    }
  }
}
</script>

<style lang="stylus" scoped>
.license-container
  page-container()

.license-page
  position relative
  width 600px
  margin-top 65px

.license__icon
  box(45px)

.license__heading
  margin-top 70px

.license__text
  width 500px
  margin-top 60px
  color $col_dark_gray
  font-size 1.2rem

  a
    color inherit

.license__cta
  margin-top 70px

.license__error-details-header
  margin-top 100px
  font-size 0.875rem

.license__error-details
  padding 20px 15px
  font-family Courier New, Courier, Lucida Sans Typewriter, Lucida Typewriter, monospace
  background-color #fff9e3

</style>
